import React from 'react';
import { Link } from "react-router-dom";

import logo from './logo.png';
import './style.css';

const Home = () => (
  <div className="Home">
    <div className="logo">
      <h1><img alt="Orthello" src={logo}/></h1>
    </div>
    <Link className="link" to="/play">
      Play
    </Link>
  </div>
);

export default Home;



